import { useFetch } from "../hooks/fetch"
import RecipeList from "../components/RecipeList"
import "./Recipes.css"
import { useEffect, useState } from "react"
import { db } from "../firebase/config"
import { collection, getDocs } from "firebase/firestore"
import { useTheme } from "../hooks/useTheme"

export default function Recipes() {
    const [data, set_data] = useState(null)
    const [error, set_error] = useState(false)
    const [isLoading, set_isLoading] = useState(false)
    const { mode } = useTheme()
    useEffect(
        () => {
            set_isLoading(true)
            const recipes_col = collection(db, 'Recipes')
            getDocs(recipes_col)
                .then(snapshoot => {
                    console.log(snapshoot.empty)
                    if (snapshoot.empty) {
                        set_isLoading(false)
                        set_error("there are no recepies or failed fo fetch it")
                    } else {
                        const result = []
                        snapshoot.docs.forEach(doc => {
                            result.push({ id: doc.id, ...doc.data() })
                        })
                        set_data(result)
                        set_isLoading(false)
                    }
                })

        },
        []
    )
    return (
        <>
            {isLoading && <p className={`loading ${mode}`}>loading...</p>}
            <div className="recipes">
                {error && <p className={`loading ${mode}`}>{error}</p>}
                {data && <RecipeList data={data} />}
            </div>
        </>
    )
}
