import "./Recipe.css"
import { useParams } from 'react-router-dom'
import { useTheme } from "../../hooks/useTheme"
import { db } from "../../firebase/config"
import { doc, getDoc } from "firebase/firestore"
import { useEffect, useState } from "react"

export default function Recipe() {
    let { id } = useParams()

    const { color } = useTheme()
    const [data, set_data] = useState(null)
    const [error, set_error] = useState(false)
    const [isLoading, set_isLoading] = useState(false)
    const { mode } = useTheme()
    useEffect(
        () => {
            set_isLoading(true)
            const ref = doc(db, "Recipes", id)
            getDoc(ref)
                .then(
                    doc => {
                        if (doc.empty) {
                            set_isLoading(false)
                            set_error("there are no recepies or failed fo fetch it")
                        } else {
                            set_isLoading(false)
                            set_data(doc.data())
                        }
                    }
                )
        },
        []
    )
    console.log(data)
    return (
        <div>
            {error && <p className={`loading ${mode}`}>{error}</p>}
            {isLoading && <p className={`loading ${mode}`}>loading...</p>}
            {data && <>
                {
                    <div className="single_card" style={color === "#E5CFF7" || color === "#FFB000" ? { backgroundColor: "#000", color: "#fff" } : { backgroundColor: "#fff" }}>
                        <h2 style={{ color: color }}>{data.title}</h2>
                        <p>{data.coocking_time}</p>
                        <ul>
                            {data.ingredients.map(ing => <li>{ing}</li>)}
                        </ul>
                        <p>{data.method}</p>
                    </div>
                }
            </>

            }
        </div>
    )
}
