import { useState } from "react"
import "./Create.css"
import { useNavigate } from "react-router-dom"
import { useTheme } from "../../hooks/useTheme"
import { addDoc } from "firebase/firestore"
export default function Create() {
    const [title, set_title] = useState("")
    const [ingredients, set_ingredients] = useState("")
    const [ingredients_array, set_ingredients_array] = useState([])
    const [method, set_method] = useState("")
    const [cooking_time, set_cooking_time] = useState(0)
    const navigate = useNavigate()
    const { color } = useTheme()

    const handle_submit = async (e) => {
        e.preventDefault()
        const data = { title, ingredients: ingredients_array, method, cookingTime: `${cooking_time} minutes` }

    }
    const add = (e) => {
        e.preventDefault()
        if (ingredients && !ingredients_array.includes(ingredients)) {
            set_ingredients_array(prev => [...prev, ingredients])
            set_ingredients("")
        }
    }

    return (
        <form className="create_form" onSubmit={handle_submit} style={{ backgroundColor: color }}>
            <label>
                Food name:
                <input type="text" onChange={(e) => set_title(e.target.value)} value={title} />
            </label>
            <label>
                Ingredients:
                <div className="ingredients">
                    <input type="text" onChange={e => set_ingredients(e.target.value)} value={ingredients} />
                    <button onClick={add}>Add</button>
                </div>
                <p>current ingredients :{ingredients_array && ingredients_array.map((ing) => <strong key={ing}>{ing} , </strong>)}</p>
            </label>
            <label className="method_label">
                method:
                <textarea cols="50" rows="10" onChange={e => set_method(e.target.value)} value={method}></textarea>
            </label>
            <label >
                Cooking time:
                <input type="number" onChange={e => set_cooking_time(e.target.value)} value={cooking_time} />
            </label>
            <button >submit</button>
        </form>
    )
}
