import { useState } from "react";
import { useTheme } from "../hooks/useTheme";
import "./Dots.css";
import img from "./brightness.svg"

const theme_colors = ["#E5CFF7", "#FFB000", "#82A0D8"];
export default function Dots() {
    const { change_color, change_mode, mode } = useTheme();
    const [active_index, set_active_index] = useState(false)
    const toggle_mode = () => {
        change_mode(mode === "dark" ? "light" : "dark")
    }
    return (
        <div className="theme-control">
            <div className="mode-wrapper">
                <img src={img} alt="" onClick={toggle_mode} style={mode === "dark" ? { filter: "invert(100%)" } : {}} />
            </div>
            <div className="dots-wrapper">
                {theme_colors.map((color, index) => (
                    <div
                        key={color}
                        className={`dot ${index === active_index ? "active" : ""}`}
                        style={
                            index === active_index ?
                                { background: color, boxShadow: `0 0 20px ${color}` } :
                                { background: color }
                        }
                        onClick={() => {
                            change_color(color)
                            set_active_index(index)
                        }}
                    ></div>
                ))
                }
            </div >
        </div>
    );
}
