import { useTheme } from "../hooks/useTheme"
import "./RecipeList.css"
import { Link } from 'react-router-dom'

export default function RecipeList({ data }) {
    const { color, change_color } = useTheme()
    return (
        <>
            {

                data.map(recipe => (
                    <div key={recipe.id} className="card" style={color === "#E5CFF7" || color === "#FFB000" ? { backgroundColor: "#000", color: "#fff", textShadow: "0px 0px 10px " + color } : { backgroundColor: "#fff", textShadow: "0px 0px 10px " + color }}>
                        <h2 style={{ color: color }}>{recipe.title}</h2>
                        <p>{recipe.coocking_time} to cook</p>
                        <p>{recipe.method.substr(0, 100)}</p>
                        <Link to={`/recipes/${recipe.id}`}>Coock this</Link>
                    </div>
                ))
            }
        </>
    )
}
