import "./Navbar.css"
import { Link } from 'react-router-dom'
import Searchbar from "./Searchbar"
import { useTheme } from "../hooks/useTheme"

export default function Navbar() {
    const { color, change_color } = useTheme()
    return (
        <div className="navbar" style={{ background: color, transition: "background 1s ease" }}>
            <Link to="/" className="logo"><h1>Opencode Food</h1></Link>
            <Searchbar />
            <Link to="/create" className="link">create</Link>
        </div>
    )
}
